import { Injectable } from '@angular/core';
import { HttpHeaders, HttpHeaderResponse } from '@angular/common/http';
import { HttpModule, Http } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';
import { map } from 'rxjs/operators';
@Injectable({
  providedIn: 'root'
})
export class Wheather1Service {
  apiKey = '8be588e0c42695baecae36f70ee98c70';
  url;
  constructor(private http: Http) {
    this.url = 'http://api.openweathermap.org/data/2.5/weather?q='
  }

  getWhether(city) {
    console.log(city,"city in getwheather service");
    console.log(this.url+city+'&APPID='+ this.apiKey, "hitting URL");
    return this.http.get(this.url+city+'&units=metric'+'&APPID='+ this.apiKey)
      .pipe(
        map((res) => res.json()));
  }
}
